.countTypeElem {
  height: 60px;
  width: 132px;
  background-image: url(../../../../assets/bg-pic.svg);
  background-repeat: no-repeat;
  position: relative;

  div {
    display: block;
    position: absolute;
    height: 16px;
    top: 50%;     
    left: 50%;                  
    transform: translate(-50%, -55%);

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    /* identical to box height, or 16px */


    color: #FFFFFF;
  }

}