.counterPage {
  width: 100%;
  height: 100%;
  // display: flex;
  // flex-direction: column;
  // align-items: center;
  position: relative;

  .containerCount {
    height: 85vh;
    display: grid;
    grid-template-rows: 26% 20% 25% 1fr;
    justify-items:center;
    align-items:center;

    .h-1 {
      // margin-bottom: 10px;

      font-family: 'Poppins';
      font-style: normal;
      font-weight: 700;
      font-size: 72px;
      line-height: 104%;

      text-align: center;
      text-transform: uppercase;

      color: #162C4E;

      opacity: 0.39;
    }

    .m-desc {
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 150%;
      // margin-bottom: 32px;
      /* or 27px */

      text-align: center;

      color: rgba(0, 0, 0, 0.8);
    }

  }


  .left-bg-pic_container {
    width: 25%;
    position: absolute;
    top: 0;
    left: 0;

    .left-bg-pic {
      width: 100%;
    }
  }

  .right-bg-pic_container {
    width: 14%;
    position: absolute;
    top: 0;
    right: 0;

    .right-bg-pic {
      width: 100%;
    }
  }
}